import React from 'react';

const SignUpValidation = (values) => {
    let errors = {};
    const EMAIL_PATTERN =  /\S+@\S+\.\S+/
    const PHONE_PATTERN = /(^(09|9)[1][1-9]\d{7}$)|(^(09|9)[3][12456]\d{7}$)/
    const PASSWORD_VALID_SIZE = 8;

    if(values.email === ""){
        errors.email = "وارد کردن ایمیل ضروری است";
    }else if(!EMAIL_PATTERN.test(values.email)){
        errors.email="ایمیل وارد شده صحیح نمی باشد";
    }

    if(values.userName === ""){
        errors.userName = "وارد کردن نام کاربری ضروری است";
    }

    if(values.password === ""){
        errors.password = "وارد کردن گذرواژه ضروری است";
    }else if(values.password.length < PASSWORD_VALID_SIZE){
        errors.password = "رمز عبور حداقل باید شامل ۸ کارکتر باشد";
    }

    if(values.name === ""){
        errors.name = "وارد کردن نام ضروری است";
    }

    if(values.rePassword !== values.password){
        errors.rePassword = "رمز عبور و تکرار آن مطابقت ندارند";
    }

    if(!PHONE_PATTERN.test(values.phoneNumber) && values.phoneNumber !== "" ){
        errors.phoneNumber = "شماره موبایل وارد شده صحیح نمی باشد";
    }

    return errors;
};

export default SignUpValidation;
