import React from 'react'
import UpButtonPic from '../../../assets/icons/UpButton.svg'
import './UpperButton.css'
const UpperButton = () => {

    const scrollToTop = () =>{
        window.scrollTo({
            top:0,
            behavior:"smooth"
        })
    }


    return(
        <React.Fragment>
            <div className='upper-button-div'>
                <div style={{display:'flex',alignItems:'center',justifyContent:'center'}} onClick={scrollToTop}>
                    <img src={UpButtonPic} alt='UpButtonPic' style={{cursor:"pointer"}}/>
                    <p>برگشت به بالا</p>
                </div>
            </div>
        </React.Fragment>
    )
}
export default UpperButton