
import {axiosAuthInstance} from './baseApi'

export const signupApi = (user,callback) => {
    axiosAuthInstance().post("/register",user)
        .then(response => {
            const data = response.data;
            callback(true,data);

        }).catch(error => {
        console.log(error);
        callback(false,error.response.data.message);

    })
}
