import React from 'react'
import './Signin.css'
import RefreshButtonPic from '../../assets/icons/RefreshButton.svg'
import Carousel from 'react-material-ui-carousel'
import FirstSliderBGPic from '../../assets/icons/FirstSliderBG.jpeg'
import SecondSliderBGPic from '../../assets/icons/SecondSliderBG.jpg'
import ThirdSliderBGPic from '../../assets/icons/ThirdSliderBG.jpg'
import {useHistory} from 'react-router-dom'
import UseSignIn from "./UseSignIn";
import signInValidation from "./SignInValidation"
const Signin = () =>
{
    const {values,errors,submitHandler,onChangeHandler} = UseSignIn(signIn,signInValidation)

    function signIn(){

    }

    const history = useHistory();
    const [userRandomNumber1,setUserRandomNumber1] = React.useState(0);
    const [userRandomNumber2,setUserRandomNumber2] = React.useState(0);
    const [sumHolder,setSumHolder] = React.useState(0);
    const [captchaValue,setCaptchaValue] = React.useState(0);

    const generateCaptcha = () => {
        let number1 = Math.floor(Math.random()*10)+1;
        let number2 = Math.floor(Math.random()*10)+1;
        setUserRandomNumber1(number1);
        setUserRandomNumber2(number2);
        let sum = number1 + number2 ;
        setSumHolder(sum);
    }
    React.useEffect(()=>{
        generateCaptcha();
    },[])
    const captchaHandler = (event) => {
        setCaptchaValue(Number(event.target.value))
    }
    const signupHandler = () => {
        history.push('/signup')
    }
    const homePageHandler = () => {
        history.push('/')
    }

    return(
        <React.Fragment>
            <div className='signin-div'>
            <div className='signin-main-div'>
                    <div className='signin-right-div'>
                        <form onSubmit={submitHandler} noValidate >
                            <div className='signin-input-div'>
                                <p className='signin-title' onClick={homePageHandler}>Daneshjoo Dong</p>
                                <input name = "email" placeholder='ایمیل' type='text' autoFocus style={{marginTop:'110px'}} required onChange={onChangeHandler} value={values.email || ""}/>
                                <div className="errors">
                                    <p>{errors.email}</p>
                                </div>
                                <input name="password" placeholder='رمز عبور' type='password' required onChange={onChangeHandler} value={values.password || ""}/>
                                <div className="errors">
                                    <p>{errors.password}</p>
                                </div>
                                <div className='signin-captcha-input-div'>
                                    <input placeholder='کد امنیتی را وارد کنید' type='number' style={{margin:'0'}}/>
                                    <div></div>
                                    <div className='signin-captcha-div'>
                                        <img src={RefreshButtonPic} alt='RefreshButtonPic' onClick={generateCaptcha}/>
                                        <p>{userRandomNumber1}</p>
                                        <p>+</p>
                                        <p>{userRandomNumber2}</p>
                                    </div>
                                </div>
                                <button type = "submit" className='signin-button'>ورود به دانشجو دنگ</button>
                                <p className='signin-go-to-signup-text' onClick={signupHandler}>ثبت نام کرده اید ؟ ابتدا ثبت نام کنید</p>
                            </div>
                        </form>
                    </div>

                <div>
                    <Carousel
                    autoPlay={true}
                    navButtonsAlwaysInvisible={true}
                    indicators={false}
                    >
                        <img className='imgs' src={FirstSliderBGPic} alt='FirstSliderBGPic'/>
                        <img className='imgs' src={SecondSliderBGPic} alt='SecondSliderBGPic'/>
                        <img className='imgs' src={ThirdSliderBGPic} alt='ThirdSliderBGPic'/>
                    </Carousel>
                </div>

            </div>
            </div>
        </React.Fragment>
    )
}
export default Signin
