import React from 'react'
import './Modal.css'
import closeButtonPic from '../../../assets/icons/closeButton.svg'

const Modal = (props) => {
    
    return(
        <React.Fragment>
            <div className='modal-main-div'>
                <div className='modal-header-div'>
                    <p>قوانین و مقررات ثبت نام در دانشجو دنگ</p>
                    <button onClick={props.closeModalHandler}>
                        <img src={closeButtonPic} alt='closeButtonPic'/>
                    </button>
                </div>
                <div className='modal-text-div'>
                    <p>دانشجو دنگ  به دوستان خوابگاهی این امکان را میدهد که بدهی ها و پرداخت های گروهی خود را مدیریت کنند</p>
                    <p> شما موافقت می کنید که می دانید این بدهی ها از نظر قانونی الزام آور نیستند و نشان دهنده اشتراک اطلاعات غیر رسمی در میان هم اتاقیها است</p>
                    <p> شما موافقت می کنید که این وبسایت نمی تواند صحت اطلاعات وارد شده توسط کاربران را تضمین کند</p>
                    <p>شما موافقت می کنید که از به کار گیری هرگونه الفاظ ناشایست در قسمت چت خودداری می کنید و نهایت ادب و احترام را رعایت می نمایید </p>
                    <p>شما موافقت می کنید که به میزان مورد نیازتان کیف پول خود را شارژ خواهید کرد و دانشجو دنگ هیچگونه تعهدی نسبت به بازگشت پول به حساب شما ندار د</p>
                    <p> از دانشجو دنگ استفاده می کنند احترام گذاشته و از آنان محافظت می کند</p>
                    <p>دانشجو دنگ هیچ گونه تعهدی در خصوص هرگونه اختلافات احتمالی بین اعضای گروه ندارد</p>
                    <p>دانشجو دنگ ممکن است که تغییراتی در قوانین و ضوابط سایت ایجاد کند که در این صورت تضمین می کند که شما را ازاین تغییرات مطلع گرداند </p>
                    <p>دانشجو دنگ تابع قوانین جمهوری اسلامی ایران است و کاربران آن نیز باید به این قوانین متعهد باشند </p>
                    <p> این شرایط شامل کل توافق نامه بین شما و دانشجو دنگ است </p>
                </div>
            </div>
        </React.Fragment>
    )
}   
export default Modal