import React from 'react'
import './LandingSlider.css'
import ShreddingTheInvoicePic from '../../../assets/icons/ShreddingTheInvoice.svg'
import SolvencyPic from '../../../assets/icons/Solvency.svg'
import MoneyExchangePic from '../../../assets/icons/MoneyExchange.svg'

function LandingSlider() {

    

    return (
        <React.Fragment>
            <div className='landing-slider-main-div'>
                <div className='landing-slider-div'>
                    <div>
                        <img src={MoneyExchangePic} alt='MoneyExchangePic'/>
                        <p>امکان تبادل پول بین کیف پول ها</p>
                    </div>
                    <div>
                        <img src={ShreddingTheInvoicePic} alt='ShreddingTheInvoicePic'/>
                        <p>امکان خرد کردن فاکتور به شیوه های مختلف</p>
                    </div>
                    <div>
                        <img src={SolvencyPic} alt='SolvencyPic'/>
                        <p>امکان پرداخت بدهی با درگاه امن</p>
                    </div>
                </div>
            </div>    
        </React.Fragment>
    );
}
export default LandingSlider