import React from 'react'
import './Toolbar.css'
const Toolbar = () => {

    const scrollToCreateGroup = () =>{
        window.scrollTo({
            top:1920,
            behavior:"smooth"
        })
    }


    return(
        <React.Fragment>
             <div className='toolbar-pic'>
                 <div className="viewOnImg"></div>
                <p className='toolbar-main-title'>هزینه های خوابگاهی خود را به راحتی مدیریت کنید</p>
                <p className='toolbar-text-first-row'>با دانشجو دنگ میتوانید با دوستان خود گروه خرجکرد تشکیل بدهید , پس از هر خرید فاکتور خرید خود را ثبت کنید </p>
                <p className='toolbar-text-second-row'>و به راحتی از بدهکاری یا بستانکاری خود با خبر شوید و بدهی خود را به دوستانتان پرداخت کنید.</p>
                <button className='toolbar-button' onClick={scrollToCreateGroup}>تشکیل گروه</button>
            </div>
        </React.Fragment>
    )
}
export default Toolbar
