
import {axiosAuthInstance} from './baseApi'

export const loginApi = (user,callback) => {
    axiosAuthInstance().post("/login",user)
        .then(response => {
            const data = response.data;
            callback(true,data);

        }).catch(error => {
            console.log(error);
            callback(false,error.response.data.message);

    })
}

