import React from 'react'
import './LandingChat.css'
import ChatPic from '../../assets/icons/Chat.svg'
const LandingChat = () => {
    return(
        <React.Fragment>
            <p className='landing-chat-title'>چت در دانشجو دنگ</p>
            <p className='landing-chat-text'>شما میتوانید با اعضای گروهتان در دانشجو دنگ صحبت کنید و نظرات خود را با اعضای گروهتان درمیان بگذارید </p>
            <div className='landing-chat-image-div'>
                <img src={ChatPic} alt='ChatPic'/>
            </div>
            <div className='landing-chat-div'>
                <p>دانشجو دنگ یک وبسایت پیشرفته مدیریت هزینه هاست. با استفاده از دانشجو درنگ میتوانید صورت حساب ها را تقسیم کنید</p>
                <p> پرداخت های خوابگاهی را به اشتراک بگذارید و هزینه های خوابگاهی خود را مدیریت کنید</p>
            </div>
        </React.Fragment>
    )
}
export default LandingChat