import React from 'react'
import './Toolbar.css'
const Toolbar = (props) => {
    return(
        <React.Fragment>
            <div className='toolbar-main-div'>
                <div className='toolbar-avatar-div'>
                    <div className='toolbar-avatar'></div>
                    <p>آرش پیریایی</p>
                </div>
                <div className='toolbar-create-div'>
                    <p>اعتبار کیف پول</p>
                    <p>۰ تومان</p>
                </div>
                <button>مشاهده وضعیت شما</button>
                <button>پرداخت بدهی</button>
                <button>افزایش اعتبار کیف پول</button>
                <button>تبادل پول بین کیف پول ها</button>
                <button>حذف مدیریت شده ی گروه</button>
                <button style={{marginBottom:'30px'}}>ملحق شدن به گروه</button>
            </div>
        </React.Fragment>
    )
}
export default Toolbar