import React from 'react'
import './Signup.css'
import RefreshButtonPic from '../../assets/icons/RefreshButton.svg'
import Carousel from 'react-material-ui-carousel'
import FirstSliderBGPic from '../../assets/icons/FirstSliderBG.jpeg'
import SecondSliderBGPic from '../../assets/icons/SecondSliderBG.jpg'
import ThirdSliderBGPic from '../../assets/icons/ThirdSliderBG.jpg'
import Modal from '../ui/modal/Modal'
import {useHistory} from 'react-router-dom'
import UseSignUp from "./useSignUp";
import signUpValidation from "./signUpValidation"
const Signup = () =>
{
    const {values,errors,submitHandler,onChangeHandler} = UseSignUp(signUp,signUpValidation)

    function signUp(){

    }

    const history = useHistory();
    const [userRandomNumber1,setUserRandomNumber1] = React.useState(0);
    const [userRandomNumber2,setUserRandomNumber2] = React.useState(0);
    const [sumHolder,setSumHolder] = React.useState(0);
    const [captchaValue,setCaptchaValue] = React.useState(0);
    const [showModal,setShowModal] =React.useState(false);

    const generateCaptcha = () => {
        let number1 = Math.floor(Math.random()*10)+1;
        let number2 = Math.floor(Math.random()*10)+1;
        setUserRandomNumber1(number1);
        setUserRandomNumber2(number2);
        let sum = number1 + number2 ;
        setSumHolder(sum);
    }

    const modalHandler = () => {
        setShowModal(true)
    }

    React.useEffect(()=>{
        generateCaptcha();
    },[])

    const captchaHandler = (event) => {
        setCaptchaValue(Number(event.target.value))
    }

    const closeModalHandler = () => {
        setShowModal(false)
    }

    const homePageHandler = () => {
        history.push('/')
    }





    return(
        <React.Fragment>
            <div className='signup-div'>
            <div className='signup-main-div'>

                <div className='signup-right-div'>
                    <form onSubmit={submitHandler} noValidate>
                        <p className='signup-main-title' onClick={homePageHandler}>Daneshjoo Dong</p>

                        <div className='signup-input-div'>
                            <div></div>
                            <div >
                                <input name="name" placeholder='نام و نام خانوادگی' type='text' autoFocus required onChange={onChangeHandler} value={values.name || ""}/>
                                <div className="errors">
                                    <p>{errors.name}</p>
                                </div>

                                <input name="password" placeholder='رمز عبور' type='password' required onChange={onChangeHandler} value={values.password || ""}/>
                                <div className="errors">
                                    <p>{errors.password}</p>
                                </div>

                                <input name="phoneNumber" placeholder='شماره تلفن همراه' type='number' onChange={onChangeHandler} value={values.phoneNumber || ""}/>
                                <div className="errors">
                                    <p>{errors.phoneNumber}</p>
                                </div>

                                <input placeholder='کد امنیتی را وارد کنید' type='number' onChange={captchaHandler}/>
                            </div>
                            <div></div>
                            <div>
                                <input name="userName" placeholder='نام کاربری' type='text' required onChange={onChangeHandler} value={values.userName || ""}/>
                                <div className="errors">
                                    <p>{errors.userName}</p>
                                </div>

                                <input name="rePassword" placeholder='تکرار رمز عبور' type='password' required onChange={onChangeHandler} value={values.rePassword || ""}/>
                                <div className="errors">
                                    <p>{errors.rePassword}</p>
                                </div>

                                <input name="email" placeholder='ایمیل' type='email' required onChange={onChangeHandler} value={values.email || ""}/>
                                <div className="errors">
                                    <p>{errors.email}</p>
                                </div>

                                <div className='signup-captcha-div'>
                                    <img src={RefreshButtonPic} alt='RefreshButtonPic' onClick={generateCaptcha}/>
                                    <p>{userRandomNumber1}</p>
                                    <p>+</p>
                                    <p>{userRandomNumber2}</p>
                                </div>
                            </div>
                            <div></div>
                        </div>
                        <button type="submit" className='signup-button'>ثبت نام در دانشجو دنگ</button>
                        <div className='signup-rule-div'>
                            <p>با ثبت نام در سایت</p>
                            <p style={{borderBottom:"0.5px solid #86C4D1",cursor:'pointer'}} onClick={modalHandler}>قوانین و مقررات</p>
                            <p>را میپذیرم</p>
                        </div>
                    </form>
                </div>

                <div>
                    <Carousel
                    autoPlay={true}
                    navButtonsAlwaysInvisible={true}
                    indicators={false}
                    >
                        <img className="imgs" src={FirstSliderBGPic} alt='FirstSliderBGPic'/>
                        <img className="imgs" src={SecondSliderBGPic} alt='SecondSliderBGPic'/>
                        <img className="imgs" src={ThirdSliderBGPic} alt='ThirdSliderBGPic'/>
                    </Carousel>
                </div>

            </div>
            </div>
            {showModal?<Modal showModal={showModal} closeModalHandler={closeModalHandler}/>:null}
        </React.Fragment>
    )
}
export default Signup
