import React from 'react';

const SignInValidation = (values) => {
    let errors = {};
    const EMAIL_PATTERN =  /\S+@\S+\.\S+/

    if(values.email === ""){
        errors.email = "وارد کردن ایمیل ضروری است";
    }else if(!EMAIL_PATTERN.test(values.email)){
        errors.email="ایمیل وارد شده صحیح نمی باشد";
    }

    if(values.password === ""){
        errors.password = "وارد کردن گذرواژه ضروری است";
    }

    return errors;
};

export default SignInValidation;
