import React from 'react'
import LandingToolbar from '../components/landing-toolbar/Toolbar'
import LandingSigmoid from '../components/landing-sigmoid/LandingSigmoid'
import LandingSlider from '../components/ui/slider/LandingSlider'
import LandingCreateGroup from '../components/landing-create-group/CreateGroup'
import LandingChat from '../components/landing-chat/LandingChat'
import UpperButton from '../components/ui/upper-button/UpperButton'
import Footer from '../components/footer/Footer'
import Header from '../components/header/Header'
const HomePage = () => {
    return(
        <React.Fragment>
            <div style={{width:'100%',height:'100%',backgroundColor:'#F9FAFB'}}>
                <Header/>
                <LandingToolbar/>
                <LandingSigmoid/>
                <LandingSlider/>
                <LandingCreateGroup/>
                <LandingChat/>
                <UpperButton/>
                <Footer/>
            </div>
        </React.Fragment>
    )
}
export default HomePage