import React, {useEffect, useState} from 'react';
import {signupApi} from "../../connect/signupApi";

const UseSignUp = (signUp,validation) => {

    const [values,setValues] = useState({name:"",userName:"",email:"",phoneNumber:"",password:"",rePassword:""});
    const[errors,setErrors] = useState({});
    const[isSubmitted,setIsSubmitted] = useState(false);

    const user = {
        name : values.name,
        username : values.userName,
        email : values.email,
        lastName : values.phoneNumber,
        password : values.password,
        confirmPassword : values.rePassword
    }


    useEffect(()=>{
        if(Object.keys(errors).length === 0 && isSubmitted){
            signupApi(user,(isOk,data)=>{
                if(isOk){
                    alert(data.message);
                    console.log(data);
                    // signUp();
                }else{
                    alert(data);
                    console.log(data.status);

                }
            })

        }
    },[errors]);

    const onChangeHandler = e =>{
        setValues({...values,[e.target.name] : e.target.value});
    }

    const submitHandler = e =>{
        e.preventDefault();
        setErrors(validation(values));
        setIsSubmitted(true);
    }

    return {values,errors,submitHandler,onChangeHandler};
};

export default UseSignUp;
