import React from 'react'
import './Footer.css'
import LinkdinPic from '../../assets/icons/Linkdin.svg'
import TwiterPic from '../../assets/icons/Twiter.svg'
import InstagramPic from '../../assets/icons/Instagram.svg'
import TelegramPic from '../../assets/icons/Telegram.svg'
import EtehadiyePic from '../../assets/icons/Etehadiye.svg'
import EnamaadPic from '../../assets/icons/Enamaad.svg'
const Footer = () => {
    return(
        <React.Fragment>
            <div className='footer-main-div'>
                <div className='footer-div'>
                    <div className='footer-social-div'>
                        <p className='footer-site-name'>Daneshjoo Dong</p>
                        <div className='footer-social-pic'>
                            <img src={TelegramPic}  alt='TelegramPic'/>
                            <img src={InstagramPic}  alt='InstagramPic'/>
                            <img src={TwiterPic} alt='TwiterPic'/>
                            <img src={LinkdinPic} className='footer-img' alt='LinkdinPic'/>
                        </div>
                    </div>
                    <div className='footer-social-div' >
                        <div className='footer-contact-div'>
                            <p>درباره ما</p>
                            <p>تماس با ما</p>
                            <p>قوانین و مقررات</p>
                            <p>ثبت شکایات</p>
                            <p>ثبت نظر</p>
                        </div>
                        <div className='footer-contact-div'>
                            <img src={EtehadiyePic} alt='EtehadiyePic'/>    
                            <img src={EnamaadPic} alt='EnamaadPic'/> 
                        </div> 
                    </div>
                    <p className='footer-detail-text' style={{marginTop:'32px'}}>با دانشجو دنگ میتوانید با دوستان خود گروه خرجکرد تشکیل بدهید و فاکتور های خرید خود را ثبت کنید و به راحتی از بدهکاری یا بستانکاری خود با خبر شوید و بدهی خود را به دوستانتان پرداخت کنید. </p>
                    <p className='footer-detail-text'>با استفاده از پیامرسان ما میتوانید با هم گروهیان خود در ارتباط باشید و هماهنگی های لازم را جهت تبادل پول بین کیف پول ها , پرداخت بدهی و .... انجام بدهید.</p>
                    <p className='footer-detail-text' >مارا به دوستان خود معرفی کنید و با استفاده از دعوت از دیگران از دوستانتان به صورت پیامکی دعوت کنید تا عضو دانشجو دنگ بشوند.</p>
                </div>
            </div>
        </React.Fragment>
    )
}
export default Footer