import React from 'react'
import './CreateGroup.css'
import Divider from '@material-ui/core/Divider';
const CreateGroup = () => {
     //if true,its a Mnager
     //if false,its a Member
    const [switchChecked,setSwitchChecked] = React.useState(true)
    const switchHandleChange = () => {
        setSwitchChecked(!switchChecked)
    }
    return(
        <React.Fragment>
            <div className='create-group-main-div'>
                <p className='create-group-title'>تشکیل گروه خرجکرد</p>
                <div className='create-group-div'>
                    <p className='create-group-div-text'>اسم گروه</p>
                    <input type='number' placeholder='مثلا اتاق ۱۰۳'/>
                    <p className='create-group-div-text'>نفرات گروه</p>
                    <input/>
                    <p className='create-group-div-text'>تعیین نقش ها در گروه</p>
                    {switchChecked?
                    <div className='create-group-toggle-div' onClick={switchHandleChange}> 
                        <div className='create-group-toggle-active-right-div'>
                            <p className='create-group-toggle-active-text'>میخواهم مدیر باشم</p>
                        </div>
                        <div className='create-group-toggle-deactive-left-div'>
                        <p className='create-group-toggle-deactive-text'>میخواهم عضو باشم</p>
                        </div>
                    </div>:
                    <div className='create-group-toggle-div' onClick={switchHandleChange}> 
                        <div className='create-group-toggle-deactive-right-div'>
                            <p className='create-group-toggle-deactive-text'>میخواهم مدیر باشم</p>
                        </div>
                        <div className='create-group-toggle-active-left-div'>
                            <p className='create-group-toggle-active-text'>میخواهم عضو باشم</p>
                        </div>
                    </div>
                    }
                    <button>ثبت گروه</button>
                </div>
            </div>
            <Divider style={{width:'80%',margin:'auto',marginTop:'128px',marginBottom:'64px',height:'2px'}}/>
        </React.Fragment>
    )
}
export default CreateGroup